import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomModelDataComponent } from './custom-model-data.component';

export const routes: Routes = [
    {
        path: '',
        component: CustomModelDataComponent,
    },
]
export const CustomModelDataRouting: ModuleWithProviders = RouterModule.forChild(routes);