import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { Message, ConfirmationService } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { CustomModelDataBean } from './bean/custom-model-data.bean';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import value from '*.json';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
@Component({
    selector: 'custom-model-data',
    templateUrl: './custom-model-data.component.html',
    styleUrls: ['./custom-model-data.component.scss'],
    providers: [ConfirmationService]
})
export class CustomModelDataComponent implements OnInit {
    code: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;
    first: number = 0;
    pageSize: number = 10;
    pageNum: number = 1;
    total: number = 0;
    msgs: Message[] = [];
    user: any;
    chName: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    modelTable: any = [];
    addPage: boolean = false;
    corresTem: boolean = false;
    isAll: boolean = false;
    tasklistChildren: any;
    addHeader = '选择字段';
    backList: any;//撤销
    tempId: any;
    modelId: any;
    tempStat: any;
    startDate: Date = null;
    endDate: Date = null;
    modelOperatorId: any;
    modelOperatorName: any;
    tableData: any = [];
    arrList: any = [];
    modelNameOptions: any = [];//模板名称
    modelStatOptions: any = [];//状态
    customModelDataBean: CustomModelDataBean = new CustomModelDataBean();
    addCorresTem: boolean = false;
    addForm: any;
    tagId: string;//
    modelDate: string;
    modelName: any;
    modelDescription: any;
    maxDate: Date = new Date();
    flag: number;
    bean: any;
    whether: boolean = true;
    constructor(
        public httpService: CusOperationHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
        public fb: FormBuilder,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.chName.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        this.userName = this.chName['userName'];
    }

    ngOnInit() {
        this.codeVal();
        this.modelStatOptions = this.code['Model_Stat'];//模型状态

        this.customModelDataBean.pageSize = 10;
        this.customModelDataBean.pageNum = 1;
        // 查询选择字段的数据
        let param = {
            businessType: "CrossSelling",
            funcType: "ModelInfo",
        }
        this.httpService.getDisplayFields(param).subscribe(data => {
            let str = data.fields;
            let checkThis = true;
            for (let item of str) {
                if (item.isCheck == true) {
                    item.isCheck = true;
                    this.tableData.push(item);
                    checkThis = item.isCheck && checkThis;
                } else if (item.isCheck == false) {
                    item.isCheck = false;
                    checkThis = false;
                }
            }
            this.arrList = str;
            this.isAll = checkThis;
        });
        this.tableData.sort((a, b) => {
            return a.num * 1 - b.num * 1
        });

        // 查询表格里的内容
        this.query();
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    showDataStart() {
        if (this.startDate && this.endDate) {
            if (this.startDate > this.endDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                setTimeout(() => {
                    this.startDate = null;
                })
            }
        }
    }
    showDataEnd() {
        if (this.startDate && this.endDate) {
            if (this.startDate > this.endDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                setTimeout(() => {
                    this.endDate = null;
                })
            }
        }
    }
    //选择字段
    chooseBtn() {
        this.addPage = true;
    }
    //单选
    doSelect(data) {
        let sb = true;
        if (data.isCheck) {
            this.tableData.push(data);
            this.arrList.forEach(item => {
                sb = item.isCheck && sb
            });
        } else {
            this.tableData.forEach((item, i) => {
                if (item.value == data.value) {
                    this.tableData.splice(i, 1);
                    return;
                }
            })
            sb = false;
        }
        this.tableData.sort((a, b) => {
            return a.num * 1 - b.num * 1
        })
        this.isAll = sb;
    }
    //全选
    checkAll() {
        let isTrue = this.isAll;
        this.tableData = [];
        this.arrList.forEach(item => {
            item['isCheck'] = isTrue;
            if (isTrue) {
                this.tableData.push(item);
            }
        });
        this.tableData.sort((a, b) => {
            return a.num * 1 - b.num * 1
        })
    }
    //选择字段保存
    save() {
        this.addMsg();
        this.addPage = false;
    }
    addMsg() {
        this.addPage = false;
        let arr = [];
        this.tableData.forEach(item => {
            arr.push(item.value)
        });
        let param = {
            businessType: "CrossSelling",
            funcType: "ModelInfo",
            fields: arr,
        }

        this.httpService.getDisplayFields(param).subscribe(data => {
            if (data.returnCode.code == 'success') {

            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    //查询
    query() {
        this.customModelDataBean.pageSize = 10;
        this.customModelDataBean.pageNum = 1;
        this.first = 0;
        this.queryModelInfoList(1);
    }
    queryModelInfoList(i, type?) {
        if (i == 2 && this.whether) {
            return
        }
        let params;
        if (i == 1) {
            params = {
                modelName: this.customModelDataBean.model_name, //模型名称
                modelDateStart: this.commfunc.transDateN(this.startDate), //推送日期-起始
                modelDateEnd: this.commfunc.transDateN(this.endDate),//	推送日期-截止
                modelStat: this.customModelDataBean.model_stat, //模型状态
                pageNum: this.customModelDataBean.pageNum,	//页码
                pageSize: this.customModelDataBean.pageSize,	//每页显示记录条数
            }
        } else if (i == 2) {
            if (!this.tagId || this.tagId == '') {
                return
            } else {
                params = {
                    tagId: this.tagId
                }
            }
        }
        this.httpService.queryModelInfoList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (i == 1) {
                    this.modelTable = data.moduleList;
                    this.total = data.totalNum;
                } else if (i == 2) {
                    if (data.bdTotalNum == 0) {
                        this.tagId = '';
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '提示', detail: '该大数据模型标识不存在！' }];
                        return
                    }
                    if (data.moduleList && data.moduleList.length > 0) {
                        this.tagId = '';
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '提示', detail: '该大数据模型已存在！' }];
                        return
                    }
                    if (type && type == 1) {
                        let params = {
                            tagId: this.tagId,
                            modelOperatorName: this.userName,
                            modelOperatorId: this.userId,
                            modelName: this.modelName,
                            modelDes: this.modelDescription
                        }
                        this.httpService.createModelInfo(params).subscribe(data => {
                            if (data.returnCode.code == 'success') {
                                this.addCorresTem = false;
                                this.query();
                            } else {
                                this.msgs = [];
                                data.returnCode.message ? data.returnCode.message : '新增失败！';
                                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                            }
                        }, err => {
                            this.msgs = [];
                            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
                        })
                    }
                }
            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }
    //重置
    reset() {
        this.startDate = null;
        this.endDate = null;
        this.customModelDataBean = new CustomModelDataBean();
    }
    //翻页
    paginate(event) {//每页显示的条数
        this.customModelDataBean.pageSize = event.rows * 1;
        //当前页
        this.customModelDataBean.pageNum = event.page + 1;
        this.first = event.page * this.customModelDataBean.pageSize;
        this.queryModelInfoList(1);
    }

    // 启用
    toUp(col) {
        let params = {
            modelId: col.modelId, //模型名称
        }
        this.httpService.queryModelTempInfoList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tasklistChildren = data.modelTempInfoList;
                this.modelId = col.modelId;
                this.corresTem = true;
            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }
    submit_1() {
        let params = {
            modelId: this.modelId, //模型名称
            modelTempId: this.tempId,
            modelOperatorId: this.userId,
            modelOperatorName: this.userName,
            createdOrgId: this.orgId,
            createdOrgName: this.chName.orgName,
            modelStat: this.tempStat = '1',
        }
        this.httpService.updateModelInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.corresTem = false;
                this.queryModelInfoList(1);
            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '修改失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }
    // 停用
    toStop(data) {
        this.confirmationService.confirm({
            message: '是否停用?',
            accept: () => {
                this.backList = data;
                this.submit_2();

            }
        });
    }
    //删除
    toDelete(col) {
        this.confirmationService.confirm({
            message: '是否删除?',
            accept: () => {
                let params = {
                    modelId: col.modelId
                }
                this.httpService.deleteModelInfo(params).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.query();
                    } else {
                        this.msgs = [];
                        data.returnCode.message ? data.returnCode.message : '修改失败！';
                        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
                })
            }
        });
    }
    //修改
    toUpdate(col) {
        this.add(2, col);
    }
    submit_2() {
        let params = {
            modelId: this.backList.modelId, //模型名称
            createdOrgId: this.orgId,
            createdOrgName: this.chName.orgName,
            modelOperatorId: this.userId,
            modelOperatorName: this.userName,
            modelStat: this.tempStat = '2',
        }
        this.httpService.updateModelInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.query();
            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '修改失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }
    //交叉销售多模型配置新增接口
    add(num, col?) {
        this.flag = num;
        console.log(col)
        if (this.flag && this.flag == 2) {
            this.whether = true;
            this.bean = col;
            this.modelName = this.bean.modelName;
            this.modelDescription = this.bean.modelDes;
            this.tagId = this.bean.tagId;
        } else {
            this.whether = false;
            this.modelName = '';
            this.modelDescription = '';
            this.tagId = '';
            this.modelDate = null;
        }
        this.addCorresTem = true;
        this.addForm = this.fb.group({
            'modelName': new FormControl('', Validators.required),
            'modelDescription': new FormControl('', Validators.required),
            'tagId': new FormControl('', Validators.required)
        });
    }
    save2() {
        //校验的
        for (const i in this.addForm.controls) {
            this.addForm.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.addForm.controls) {
            if (!this.addForm.controls[i].valid) {
                return;
            }
        }

        // let modelName, modelDes;
        // if (this.modelName && this.modelName.lenght != 0) {
        //     modelName = this.modelName.filter((item) => {
        //         return item.value == this.tagId
        //     })
        // }
        // if (this.modelDescription && this.modelDescription.lenght != 0) {
        //     modelDes = this.modelDescription.filter((item) => {
        //         return item.value == this.tagId
        //     })
        // }
        if (this.flag == 1) {
            this.queryModelInfoList(2, 1);
        } else {
            this.bean.modelName = this.modelName;
            this.bean.modelDes = this.modelDescription;
            this.bean.tagId = this.tagId;
            this.httpService.updateModelInfo(this.bean).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.addCorresTem = false;
                    this.query();
                } else {
                    this.msgs = [];
                    data.returnCode.message ? data.returnCode.message : '修改失败！';
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
            })
        }
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}
